*, :before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  height: 100vh;
  background: #ddd;
  place-items: center;
  padding: 1rem;
  font-family: Lato, sans-serif;
  display: grid;
}

.author {
  color: #777;
  font-size: 1.5rem;
  position: absolute;
  bottom: 1%;
  right: 1%;
}

.author a:link, .author a:visited {
  color: inherit;
  text-decoration: none;
}

.card {
  max-width: 27rem;
  width: 100%;
  height: 50rem;
  border-radius: 2px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 8px 28px -9px #00000073;
}

.card:hover .details {
  padding-bottom: 2.5rem;
}

.card--front {
  height: 100%;
  width: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  background: #333;
  grid-template-rows: 1fr repeat(3, min-content);
  transition: all .5s;
  display: grid;
}

.card--front .image {
  z-index: 1;
  opacity: .3;
  height: 30rem;
  transition: all .5s;
  position: relative;
}

.card--front .image:after {
  height: 100px;
  content: "";
  width: 100%;
  z-index: 1;
  background: linear-gradient(#09020400, #444);
  position: absolute;
  top: 200px;
}

.card--front .details {
  text-align: center;
  flex-direction: column;
  justify-content: flex-end;
  gap: 1rem;
  padding-top: 4rem;
  transition: all .5s;
  display: flex;
}

@media (any-hover: none) {
  .card--front .details {
    padding-bottom: 2.5rem;
  }
}

.card--front .details .title {
  color: #aaa;
  text-transform: uppercase;
  letter-spacing: .5px;
  font-size: 2rem;
  font-weight: 400;
}

.card--front .details .artist {
  color: #777;
  letter-spacing: 1px;
  font-size: 1.8rem;
}

.card--front .progress {
  height: 4px;
  width: 100%;
  cursor: pointer;
  background: #777;
}

.card--front .progress__bar {
  height: 4px;
  width: 0;
  background: #ccc;
}

.card--back {
  height: 99%;
  width: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  opacity: 0;
  visibility: hidden;
  z-index: 11;
  align-items: center;
  padding: 1rem;
  transition: all .5s;
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  transform: translateX(10rem);
}

.card--back ul {
  width: 100%;
  height: 100%;
  display: grid;
}

.card--back ul li {
  letter-spacing: .5px;
  cursor: pointer;
  color: #bbb;
  grid-template-columns: min-content 1fr;
  align-items: center;
  column-gap: 1rem;
  font-size: 1.7rem;
  font-weight: 400;
  display: grid;
}

.card--back ul li span {
  letter-spacing: .5px;
  text-transform: initial;
  color: #777;
  grid-column: 2;
  font-size: 1.5rem;
  display: block;
}

.card--back ul li:not(:last-child) {
  border-bottom: 1px solid #777;
}

.line {
  height: 100rem;
  width: 100rem;
  opacity: .6;
  border: 1px solid #fff;
  border-radius: 40%;
  animation: 85s linear infinite line;
  position: absolute;
  top: -82rem;
  left: -39rem;
}

.line:nth-child(2) {
  animation-duration: 40s;
  top: -81.9rem;
}

.line:nth-child(3) {
  animation-duration: 35s;
  top: -81.9rem;
}

.playing.line {
  animation: 5s linear infinite line;
}

.playing.line:nth-child(2) {
  animation-duration: 7s;
}

.playing.line:nth-child(3) {
  animation-duration: 9s;
}

@keyframes line {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.card--front:hover .controls .button--next-page {
  opacity: 1;
}

.card--front:hover .controls {
  opacity: 1;
  visibility: visible;
}

.card--front .controls {
  opacity: 0;
  visibility: hidden;
  justify-content: space-between;
  transition: all .5s;
  display: flex;
}

@media (any-hover: none) {
  .card--front .controls {
    opacity: 1;
    visibility: visible;
  }
}

.card--front .controls .button {
  cursor: pointer;
  background: none;
  border: none;
}

.card--front .controls .button svg {
  fill: #aaa;
