@media (any-hover: none) {
  .card--front .controls .button--next-page {
    opacity: 1;
    visibility: visible;
  }
}

.card--front .controls .button--state-active {
  background: #888;
}

.card--front .controls .button--state-active svg {
  fill: #eee;
}

.card--front .controls .hide {
  display: none;
}

.card--back:hover .button--prev-page {
  opacity: 1;
}

.card--back .btn-pause--back svg {
  scale: .6;
}

.card--back button {
  cursor: pointer;
  height: 4rem;
  width: 4rem;
  opacity: 1;
  background: none;
  border: none;
  grid-row: 1 / span 2;
}

.card--back button svg {
  fill: #aaa;
  scale: .8;
}

.card--back .button--prev-page {
  opacity: 0;
  transition: all .5s;
  position: absolute;
  top: 0;
  right: 0;
}

@media (any-hover: none) {
  .card--back .button--prev-page {
    opacity: 1;
    visibility: visible;
  }
}

.card--back .button--prev-page svg {
  scale: .6;
}

.card--back .hide {
  display: none;
}

.card--back.show {
  opacity: 1;
  visibility: visible;
  transform: translateX(0);
}

.card--front.hide .image, .card--front.hide .details, .card--front.hide .controls {
  opacity: 0;
  visibility: hidden;
}

